
.v-cal-heading{display:-ms-flexbox;display:flex;-ms-flex:1;flex:1;-ms-flex-pack:justify;justify-content:space-between
}
.v-cal-heading .v-cal-date{margin:auto
}
.v-cal-heading .v-cal-prev,.v-cal-heading .v-cal-next{padding:10px;font-size:24px;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center;border:0;background-color:transparent;outline:0
}
.v-cal-heading .v-cal-prev:hover,.v-cal-heading .v-cal-next:hover{background-color:#eee
}
.v-cal-heading .v-cal-prev:active,.v-cal-heading .v-cal-next:active{background-color:#ddd
}



